.btn-nav {
  position: relative;
  vertical-align: top;
  margin-top: 10px;
  padding: 5px 10px 5px 40px;
  background: none;
}

.no-touch .btn-nav:hover .btn-nav__icon:before, .no-touch .btn-nav:hover .btn-nav__icon:after,
.no-touch .btn-nav:hover .btn-nav__icon i {
  border-color: #fc0;
}

.no-touch .btn-nav:hover .btn-nav__icon:before {
  transform: translate3d(0, -3px, 0);
}

.no-touch .btn-nav:hover .btn-nav__icon:after {
  transform: translate3d(0, 3px, 0);
}

.no-touch .btn-nav:hover .btn-nav__text {
  color: #fc0;
}

.no-touch .btn-nav.is-active:hover .btn-nav__icon:before {
  transform: translate3d(4px, -3px, 0) rotate(45deg);
}

.no-touch .btn-nav.is-active:hover .btn-nav__icon:after {
  transform: translate3d(4px, 3px, 0) rotate(-45deg);
}

.btn-nav.is-active .btn-nav__icon:before {
  transform: translate3d(4px, -3px, 0) rotate(45deg);
}

.btn-nav.is-active .btn-nav__icon:after {
  transform: translate3d(4px, 3px, 0) rotate(-45deg);
}

.btn-nav.is-active .btn-nav__icon i {
  transform: scaleX(0);
}

.btn-nav__icon {
  position: absolute;
  top: 16px;
  left: 15px;
  height: 15px;
}

.btn-nav__icon:before, .btn-nav__icon:after,
.btn-nav__icon i {
  position: absolute;
  left: 0;
  width: 28px;
  border-bottom: 1px solid #fff;
  transition: transform 0.175s ease, border-color 0.25s ease;
}

.btn-nav__icon:before, .btn-nav__icon:after {
  content: "";
  transform: translate3d(0, 0, 0);
}

.btn-nav__icon:before {
  top: 0;
  transform-origin: top left;
}

.btn-nav__icon:after {
  bottom: 0;
  transform-origin: bottom left;
}

.btn-nav__icon i {
  top: 7px;
}

