  max-height: 100%;
  animation: fly 4s infinite ease;
}

.souvenir__content {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
}

.souvenir__title, .souvenir__description {
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
}

.souvenir__title {
  font-family: "Lato Black", Arial, sans-serif;
  font-size: 14px;
  line-height: 2.58;
  text-transform: uppercase;
}

.souvenir__description {
  font-family: "Lato Light", Arial, sans-serif;
  font-size: 18px;
  line-height: 1.34;
}

@keyframes fly {
  0%, 100% {
    transform: translate3d(0, -15px, 0) rotate(0);
  }
  50% {
    transform: translate3d(0, 15px, 0) rotate(-5deg);
  }
}

.souvenirs-bg {
  position: absolute;
  z-index: -1;
  width: 2000px;
}

.souvenirs-background {
  position: absolute;
  top: -100px;
  left: 0;
  right: 0;
  display: none;
}

.souvenirs-background img {
  position: absolute;
  left: 50%;
  transform: translate3d(-50%, 0, 0);
}

.souvenirs-background__shining2 {
  top: -230px;
}

.souvenirs-background__shining1 {
  top: 45px;
}

.souvenirs-background__light2 {
  top: 65px;
}

.souvenirs-background__light1 {
  top: 55px;
}

.souvenirs-background__shadow {
  top: 277px;
}

.souvenirs-background__light1, .souvenirs-background__light2 {
  opacity: 0.5;
}
