.form-control {
	display: block;
	padding: 0 10px;
	border: 2px solid rgba(255, 255, 255, 0.33);
	color: $white;
	transition: border-color 0.25s ease, box-shadow 0.25s ease;
	cursor: text;
	@include clr;
	&.has-focus {
		border-color: $white;
		box-shadow: 0 0 21px rgba(0, 222, 255, 0.09); }
	&.has-error {
		border-color: $red; }
	&__label {
		float: left;
		@include f('lr', 16, 30);
		padding: 4px 14px 4px 0;
		user-select: none; }
	&__field {
		display: block;
		overflow: hidden; }
	&__input,
	&__textarea {
		display: block;
		border: none;
		width: 100%;
		color: $white;
		background: none; }
	&__input {
		height: 38px;
		@include f('lr', 16, 30); }
	&__textarea {
		padding: 8px 0;
		height: 138px;
		resize: none;
		@include f('lr', 16, 24); } }
